.username{
  text-align: center;
  background-color: rgb(164, 53, 135);
  height: 300px;
  font-size: 20px;

}
.username input{
  
  padding: 10px 20px;
  border-radius: 10px;
  border-style: none;
  font-size: 14px;
}
.username button{
  padding: 10px 15px;
  margin-bottom: 50px;
  border-radius: 10px;
  border: none;
  font-size: 14px;
  cursor: pointer;
}
 nav{
  background-color: cyan;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  padding: 10px 0;
}
nav ul {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  width: 60%;
}
nav ul li{
  cursor: pointer;
  color: rgb(125, 10, 85);
  font: size 16px;
  font-weight: bold;
  transition:all 0.5s ease-in-out
}
nav ul li:hover{
  transform: scale(1.4);
  /* color:darkmagenta ; */
}
.navActive
{
  color:darkgoldenrod;
}